.container {
  width: 100%;
  height: 25rem;
  z-index: 0;
  overflow: hidden;
}

.container img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  transform: rotateZ(-4deg) translateY(-4rem) translateX(-1rem);
}